@media screen and (min-width: 1025px) {
  .p-form__area {
    position: relative;
  }
}
.p-form__area input[type=text],
.p-form__area input[type=email] {
  width: 100%;
  height: 40px;
  box-shadow: inset 0.75px 0.75px 1.5px rgba(73, 73, 73, 0.4);
  border-radius: 4px;
  margin-top: 9.5px;
  background-color: #FDFDFD;
  font-size: 200%;
}
@media screen and (min-width: 1025px) {
  .p-form__area input[type=text],
.p-form__area input[type=email] {
    height: 36px;
  }
}
.p-form__area textarea {
  width: 100%;
  height: 105px;
  box-shadow: inset 0.38px 0.38px 0.76px rgba(73, 73, 73, 0.4);
  border-radius: 2px;
  margin-top: 9.5px;
  background-color: #FDFDFD;
}
@media screen and (min-width: 1025px) {
  .p-form__area textarea {
    height: 172px;
  }
}
.p-form__area input[type=submit] {
  display: block;
  width: 123px;
  height: 37px;
  background: rgba(176, 207, 213, 0.3);
  border: 1px solid #B1D3DD;
  border-radius: 8px;
  margin: 24px auto 0 auto;
}
@media screen and (min-width: 1025px) {
  .p-form__area input[type=submit] {
    width: 120px;
    height: 36px;
    margin: 20px 0 0 auto;
  }
}
@media screen and (min-width: 1025px) {
  .p-form__one-line {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
  }
}
@media screen and (min-width: 1025px) {
  .p-form__name {
    display: flex;
    flex-direction: column;
    width: calc(50% - 20px);
  }
}
.p-form__kana {
  margin-top: 20px;
}
@media screen and (min-width: 1025px) {
  .p-form__kana {
    display: flex;
    flex-direction: column;
    width: calc(50% - 20px);
    margin-top: 0;
  }
}
.p-form__mail {
  margin-top: 20px;
}
@media screen and (min-width: 1025px) {
  .p-form__mail {
    display: flex;
    flex-direction: column;
    width: calc(50% - 20px);
    margin-top: 16px;
  }
}
.p-form__message {
  margin-top: 20px;
}
@media screen and (min-width: 1025px) {
  .p-form__message {
    margin-top: 18px;
  }
}